export function blogSlider() {
    let $blogSlider = $(".featured-news .news-holder");

    $blogSlider.slick({
        mobileFirst: true,
        slidesToShow: 1.21,
        slidesToScroll: 1,
        speed: 800,
        arrows: false,
        dots: true,
        infinite: false,
        responsive: [
            {
                breakpoint: 768,
                settings: "unslick"
            }
        ]
    });
}


