export function almondProductsSlider() {
    let $sliderSection = $(".round-card-slider");

    $sliderSection.each(function () {
        let slider = $(this);

        slider.slick({
            infinite: false,
            slidesToScroll: 1,
            slidesToShow: 1.25,
            mobileFirst: true,
            arrows: false,
            dots: false,
            responsive: [
                {
                    breakpoint: 568,
                    settings: {
                        slidesToShow: 2.4
                    }
                },
                {
                    breakpoint: 768,
                    settings: {
                        slidesToShow: 3.4
                    }
                },
                {
                    breakpoint: 1200,
                    settings: {
                        slidesToShow: 4.4
                    }
                },
            ]
        });
    });
};


