export function testimonialSlider() {
    let $sliderConntainer = $(".testimonial-slider");

    $sliderConntainer.each(function () {
        let slider = $(this);

        slider.slick({
            slidesToShow: 1,
            slidesToScroll: 1,
            infinite: true,
            speed: 500,
            fade: true,
            autoplay: true,
            autoplaySpeed: 12000,
            dots: true,
            arrows: false,
        });
    });
};


