export function newsGridSlider() {
    const slider = $(".news-grid-slider");

    function sliderInit() {
        slider.slick({
            infinite: false,
            slidesToShow: 1,
            mobileFirst: true,
            dots: true,
            arrows: false,
            responsive: [
                {
                    breakpoint: 568,
                    settings: {
                        slidesToShow: 2
                    }
                },
                {
                    breakpoint: 1199,
                    settings: "unslick"
                }
            ]
        });
    }

    $(window).on("load resize", function () {
        if (window.innerWidth < 1200 && !slider.hasClass("slick-initialized")) {
            sliderInit();
        }
    });
}


