export function customSelect() {
    if ($( "select" ).not($(".woocommerce-page select")).length) {
        jcf.setOptions("Select", {
            wrapNative: false,
            fakeDropInBody: true,
            maxVisibleItems: 8
        });
        jcf.replaceAll();
    }

    if ($(".jcf-scrollable").length) {
        jcf.replaceAll();
    }
}



